n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #040404;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop-big: 1400px,\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n        mobile-very-small: 360px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: 0 auto;\n  padding: 0 20px;\n  @include screen-lt(desktop-big) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.story {\n  position: relative;\n  min-height: 800px;\n  margin-top: 40px;\n  &__container {\n    @include container-size();\n    z-index: 2;\n    h2 {\n      margin-top: 40px;\n      margin-bottom: 40px;\n      @include screen-lt(tablet) {\n        font-size: 8vw;\n      }\n      @include screen-lt(mobile-small) {\n        font-size: 8vw;\n      }\n    }\n  }\n  &__item {\n    background-color: $white;\n    border-radius: 5px;\n    border-bottom: 17px solid $orange;\n    padding: 20px;\n    &:hover {\n      transform: scale(1.04);\n      transition: all 0.2s ease-in-out;\n    }\n    h2 {\n      font-size: 16px;\n      margin-top: 0;\n      color: $black;\n      @include main-font(700);\n      margin-bottom: 3px;\n      @include screen-lt(tablet) {\n        font-size: 1.9vw;\n      }\n      @include screen-lt(mobile) {\n        font-size: 2.5vw;\n      }\n      @include screen-lt(mobile-small) {\n        font-size: 3.5vw;\n      }\n    }\n  }\n  &__attr {\n    @include main-font(700);\n    font-size: 16px;\n    margin-bottom: 10px;\n    color: $black;\n    @include screen-lt(tablet) {\n      font-size: 1.9vw;\n    }\n    @include screen-lt(mobile) {\n      font-size: 2.5vw;\n    }\n    @include screen-lt(mobile-small) {\n      font-size: 3.5vw;\n    }\n  }\n  &__message {\n    color: $black;\n    @include main-font(400);\n    font-size: 16px;\n    @include screen-lt(tablet) {\n      font-size: 1.9vw;\n    }\n    @include screen-lt(mobile) {\n      font-size: 2.5vw;\n    }\n    @include screen-lt(mobile-small) {\n      font-size: 3.5vw;\n    }\n  }\n}\n\n.my-masonry-grid {\n  display: -webkit-box; /* Not needed if autoprefixing */\n  display: -ms-flexbox; /* Not needed if autoprefixing */\n  display: flex;\n  margin-left: -30px; /* gutter size offset */\n  width: auto;\n}\n.my-masonry-grid_column {\n  padding-left: 30px; /* gutter size */\n  background-clip: padding-box;\n}\n\n/* Style your items */\n.my-masonry-grid_column > div { /* change div to reference your elements you put in <Masonry> */\n  margin-bottom: 30px;\n}"]}]);

