   }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #040404;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop-big: 1400px,\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n        mobile-very-small: 360px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: 0 auto;\n  padding: 0 20px;\n  @include screen-lt(desktop-big) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.buy {\n  padding-bottom: 100px;\n  @include screen-lt(mobile) {\n    padding-bottom: 20px;\n  }\n  &__container {\n    flex-wrap: wrap;\n  }\n  &__item {\n    width: calc((100% / 5) - 15px);\n    height: 90px;\n    border: 1px solid #000000;\n    background-color: #fff;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    margin-right: 15px;\n    margin-bottom: 30px;\n    border-radius: 5px;\n    &:nth-child(5n - 5) {\n      margin-right: 0;\n    }\n    &:hover {\n      transform: scale(1.04);\n      transition: all 0.2s ease-in-out;\n    }\n    @include screen-lt(mobile) {\n      height: 50px;\n      width: calc((100% / 5) - 5px);\n      margin-right: 5px;\n    }\n    @include screen-lt(mobile-small) {\n      height: 50px;\n      width: calc((100% / 2) - 5px);\n      margin-right: 5px;\n      margin-bottom: 5px;\n    }\n  }\n}\n\n.buy__link {\n  display: flex;\n  width: 100%;\n  align-items: center;\n  height: 100%;\n  justify-content: center;\n  img {\n    width: 45% !important;\n  }\n  @include screen-lt(tablet) {\n    img {\n      width: 75% !important;\n    }\n  }\n  @include screen-lt(mobile-small) {\n    img {\n      width: 50% !important;\n    }\n  }\n}"]}]);

