en-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: 0 auto;\n  padding: 0 20px;\n  @include screen-lt(desktop-big) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.products {\n  position: relative;\n  background-image: url('../../../../statics/image_bacground_header.png');\n  background-size: cover;\n  background-repeat: no-repeat;\n  min-height: 800px;\n  background-position: center bottom;\n  @include screen-lt(mobile) {\n    background-position: 75% 50%;\n    min-height: 850px;\n  }\n  @include screen-lt(mobile-small) {\n    background-position: 71% 50%;\n    min-height: 850px;\n  }\n  &__container {\n    @include container-size();\n    z-index: 2;\n  }\n  &__info {\n    align-items: center;\n    justify-content: center;\n    background-color: #000;\n    width: auto;\n    padding: 10px 30px;\n    margin: 40px 0;\n    p {\n      color: $white;\n      @include main-font(600);\n      font-size: 16px;\n      @include screen-lt(tablet) {\n        font-size: 1.9vw;\n      }\n      @include screen-lt(mobile) {\n        font-size: 2.7vw;\n      }\n    }\n    @include screen-lt(mobile-small) {\n      margin: 20px 0;\n    }\n  }\n}\n\n.main__title {\n  h2 {\n    line-height: 1.2;\n    text-transform: uppercase;\n    font-size: 94px;\n    @include main-font(700);\n    @include screen-lt(desktop) {\n      font-size: 8vw;\n    }\n    @include screen-lt(mobile) {\n      font-size: 11vw;\n    }\n  }\n  span {\n    color: $orange;\n  }\n}"]}]);

