cy iOS */\n}\n","@import \"fonts.scss\";\n\n$black: #0a0b0a;\n$orange: #FF6400;\n$white: #fff;\n$grey: #8b9497;\n$bgDark: #1a1a1a;\n$brown: #484848;\n\n@mixin base-sizes() {\n  &__size {\n    &_l {\n      width: 190px;\n    }\n    &_s {\n      width: 85px;\n    }\n    &_full {\n      width: 100%;\n      height: 44px;\n    }\n  }\n}\n\n@mixin placeholder-color($color, $padding) {\n  &::-webkit-input-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n\n  &::-moz-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n\n  &:-moz-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n\n  &:-ms-input-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n}\n\n@mixin base-margins() {\n  &__margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #040404;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop-big: 1400px,\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n        mobile-very-small: 360px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: 0 auto;\n  padding: 0 20px;\n  @include screen-lt(desktop-big) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.form {\n  position: relative;\n  margin-top: 60px;\n  &:after {\n    content: '';\n    position: absolute;\n    top: 100px;\n    left: 0;\n    background: url('../../../../statics/drakon.png');\n    background-size: contain;\n    background-repeat: no-repeat;\n    height: 470px;\n    width: 770px;\n    @include screen-lt(desktop-big) {\n      width: 600px;\n      height: 370px;\n      top: 130px;\n    }\n    @include screen-lt(desktop) {\n      width: 550px;\n      height: 320px;\n      top: 130px;\n    }\n    @include screen-lt(tablet) {\n      width: 470px;\n      height: 240px;\n      top: 220px;\n    }\n    @include screen-lt(mobile) {\n      position: static;\n      display: none;\n    }\n  }\n  &__container {\n    justify-content: space-between;\n    @include container-size();\n    z-index: 2;\n    p {\n      font-size: 22px;\n      color: $white;\n      @include main-font(300);\n      @include screen-lt(tablet) {\n        font-size: 2.5vw;\n      }\n      @include screen-lt(mobile) {\n        font-size: 4.5vw;\n      }\n      span {\n        text-transform: uppercase;\n        @include main-font(600);\n      }\n    }\n    @include screen-lt(mobile) {\n      flex-direction: column;\n    }\n  }\n  &__image {\n    position: relative;\n  }\n  &__column {\n    width: 45%;\n    @include screen-lt(mobile) {\n      width: 100%;\n      &:last-child {\n        margin-top: 40px;\n      }\n    }\n  }\n}"]}]);

