800;\n  src: url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.eot'); /* IE9 Compat Modes */\n  src: local('Montserrat ExtraBold'), local('Montserrat-ExtraBold'),\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.woff2') format('woff2'), /* Super Modern Browsers */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.woff') format('woff'), /* Modern Browsers */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.ttf') format('truetype'), /* Safari, Android, iOS */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.svg#Montserrat') format('svg'); /* Legacy iOS */\n}\n","@import \"fonts.scss\";\n\n$black: #0a0b0a;\n$orange: #FF6400;\n$white: #fff;\n$grey: #8b9497;\n$bgDark: #1a1a1a;\n$brown: #484848;\n\n@mixin base-sizes() {\n  &__size {\n    &_l {\n      width: 190px;\n    }\n    &_s {\n      width: 85px;\n    }\n    &_full {\n      width: 100%;\n      height: 44px;\n    }\n  }\n}\n\n@mixin placeholder-color($color, $padding) {\n  &::-webkit-input-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n\n  &::-moz-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n\n  &:-moz-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n\n  &:-ms-input-placeholder {\n    color: $color;\n    text-transform: uppercase;\n    padding-top: $padding;\n  }\n}\n\n@mixin base-margins() {\n  &__margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #040404;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop-big: 1400px,\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n        mobile-very-small: 360px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: 0 auto;\n  padding: 0 20px;\n  @include screen-lt(desktop-big) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.prize {\n  position: relative;\n  margin-top: 40px;\n  &__container {\n    @include container-size();\n    z-index: 2;\n    h2 {\n      @include screen-lt(tablet) {\n        font-size: 6vw;\n      }\n      @include screen-lt(mobile) {\n        font-size: 8vw;\n      }\n    }\n  }\n  &__wrapper {\n    border: 1px dashed $orange;\n    border-radius: 5px;\n    padding: 20px;\n    margin-top: 40px;\n    @include screen-lt(mobile-small) {\n      flex-direction: column;\n    }\n  }\n  &__item {\n    width: 100%;\n    position: relative;\n    display: flex;\n    align-items: center;\n    //max-height: 300px;\n    @include screen-lt(mobile-small) {\n      margin-top: 50px;\n    }\n    img {\n      max-height: 297px;\n      @include screen-lt(tablet) {\n        max-height: 170px;\n      }\n    }\n    p {\n      font-size: 16px;\n      margin-top: 20px;\n      color: $white;\n      @include main-font(400);\n      span {\n        @include main-font(600);\n      }\n      @include screen-lt(tablet) {\n        font-size: 1.9vw;\n      }\n      @include screen-lt(mobile-small) {\n        font-size: 5vw;\n      }\n    }\n    &:after {\n      content: '';\n      position: absolute;\n      top: 40%;\n      right: 60px;\n      background-size: contain;\n      width: 98px;\n      height: 98px;\n      @include screen-lt(tablet) {\n        width: 50px;\n        height: 50px;\n        background-size: cover !important;\n        right: 20px;\n      }\n    }\n  }\n  .product_1 {\n    &:after {\n      background: url('../../../../statics/3x.png');\n    }\n  }\n  .product_2, .product_3 {\n    &:after {\n      top: 10%;\n      background: url('../../../../statics/5x.png');\n    }\n  }\n  .product_3 {\n    &:after {\n      right: 100px;\n      @include screen-lt(tablet) {\n        right: 50px;\n      }\n    }\n  }\n  .product_2 {\n    &:after {\n      top: 36%;\n      right: 100px;\n      @include screen-lt(tablet) {\n        right: 50px;\n      }\n    }\n  }\n}"]}]);

